
.tag-list > ul > li {
  display: inline-block;
}
.tag-list > ul > li:not(:last-child) {
    margin-right: 1rem;
}
.tag-list .tag-input {
  max-width: 20rem;
  padding: 1rem;
  margin-top: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: inherit;
}
.tag-list .tag-input.sim-autofinder {
    padding: 0;
}
.tag-list .tag-input.sim-autofinder .sim-icontext {
      display: none;
}
.tag-list .tag-input.sim-autofinder input {
      padding: 0.25rem 0.5rem;
      margin: 0;
      color: inherit;
}



