
.event-scheduler-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.event-scheduler-footer .save-draft {
    visibility: hidden;
    padding: 1rem;
    border-radius: 4px;
    background-color: #ccc;
    color: #999;
}
.event-scheduler-footer input[type=submit] {
    padding: 1rem;
    background-color: orange;
    color: white;
    border-radius: 4px;
}



