
.summary-block {
  border-radius: 0.25em;
  margin: 0.8em 0;
}
.summary-block .header {
    border-radius: 0.25em 0.25em 0 0;
    background-color: #222;
    padding: 0.6em 1em;
    position: relative;
}
.summary-block .header .title {
      font-size: 1.3em;
      color: #fff;
      letter-spacing: 0.02em;
      font-weight: bold;
}
.summary-block .header .action {
      right: -8pt;
      top: -8pt;
      position: absolute;
      outline: none;
      display: none;
}
.summary-block .header .action button.close {
        display: none;
        background-color: white;
        padding: 0.075rem 0.2rem;
        border-radius: 50%;
        color: indianred !important;
        opacity: 1;
        font-size: 1.2em;
}
.summary-block .header .action button.close i.fa {
          color: indianred !important;
}
.summary-block .body {
    border-radius: 0 0 0.25em 0.25em;
    background-color: #333;
    padding: 0.5em 1em 1em 1em;
    color: #fff;
}
.summary-block:hover {
  -webkit-box-shadow: 0 0 1px 2px #4A93B9;
          box-shadow: 0 0 1px 2px #4A93B9;
}
.summary-block:hover .header .title {
    color: #4A93B9;
}
.summary-block:hover .header .action {
    display: block;
}
.summary-block:hover .header .action button.close {
      display: block;
}



