
.autofinder-details {
  max-width: 25rem;
  max-height: 15rem;
  overflow-y: auto;
  -webkit-box-shadow: 4px 4px 5px #111;
          box-shadow: 4px 4px 5px #111;
  background-color: #333;
}
.autofinder-details li {
    padding: 0;
    margin: 0;
}
.autofinder-details li .autofinder-detail-item:hover, .autofinder-details li .autofinder-detail-item.selected {
      background-color: #555;
}
.autofinder-details h3 {
    padding: 0.5rem 0.25rem;
    margin-bottom: 0;
    font-size: 0.85rem;
}



