
.session-listing {
  width: 100%;
  background-color: #222;
}
.session-listing header {
    background-color: #666;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.session-listing header .remove > span {
      color: white;
      cursor: pointer;
}
.session-listing main > ul > li, .session-listing main > ul fieldset {
    padding: 0;
}
.session-listing main h5 {
    margin-bottom: 0;
}
.session-listing img {
    fill: white;
}



