
.mentor {
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: transparent;
  font-weight: bold;
  font-size: 1.3em;
  padding: .25em 0.5em;
  outline: none;
}
.mentor:active, .mentor:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.mentor:active {
    color: #4A93B9;
}
input[type="number"].number {
  display: inline-block;
  width: 4rem;
  text-align: center;
}
input[type="number"].number::-webkit-outer-spin-button, input[type="number"].number::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}



